.menu-item {
  font-family: 'Magorian Regular', serif;
  font-weight: 800;
  font-size: 3.4rem;
  text-align: center;
  cursor: pointer;
  margin-bottom: 1rem;
}

.menu-item-image {
  height: 30rem;
  border: #818181 solid .1rem;
  margin-bottom: .5rem;
  transition: all .5s ease-in-out;

  &:hover {
    transform: scale(1.03) translate3d( 0, 0, 0);
  }
}