.houses-overview {
  display: flex;
  margin-top: 10rem; 
  padding: 0 2rem;
}

.house {
  flex: 2;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-size: 4rem;
  cursor: pointer;
  animation: fadeIn .25s ease-in-out;
}

.house-image-container {
  margin-bottom: 1rem;
  transition:all .5s ease-in-out;

  &:hover {
    transform: scale(1.03);
  }
  
  .house-image {
    width: 25rem;
    border-radius: 1rem;
  }
}

